$color-1: #f6ead4;
$color-2: #a2a595;
$color-3: #b4a284;
$color-4: #201e20;
@font-face {
  font-family: "font-1";
  src: url(./assets/fonts/JosefinSans-ExtraLight.ttf);
}
@font-face {
  font-family: "font-2";
  src: url(./assets/fonts/JosefinSans-Regular.ttf);
}
@font-face {
  font-family: "font-3";
  src: url(./assets/fonts/JosefinSans-Bold.ttf);
}
$font-1: "font-1", sans-serif;
$font-2: "font-2", serif;
$font-3: "font-3", serif;
* {
  font-family: $font-2;
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
a {
  color: black;
  text-decoration: none;
  &:visited {
    color: black;
  }
}
